.terminal-container{font-family:'Inconsolata', monospace;background:#222c31;color:#fff;height:300px;padding:20px;border-radius:10px;overflow-y:auto;box-shadow:-4px 2px 33px 16px rgba(0,0,0,0.3)}.terminal-container .line{float:left;width:100%}.terminal-container .blink{display:inline-block;background:#fff;min-width:2px;min-height:10px;animation:blink 1s step-start 0s infinite}@keyframes blink{50%{opacity:0}}
